/* rolls */
.rolls {
	margin-bottom: 4rem;
}

.rolls:last-child {
	margin-bottom: 0;
}

.rolls__item {
	padding: 10px;
	margin-bottom: 2rem;
}

.rolls__item:last-child {
	margin-bottom: 0;
}

.rolls__header {
	position: relative;
}

.rolls__angle {
	display: block;
	position: absolute;
	right: 10px;
	top: 50%;
	text-decoration: none;
	width: 24px;
	height: 24px;
	border-radius: 50%;
	margin-top: -12px;
	background-color: tomato;
}

.rolls__angle::before, .rolls__angle::after {
	content: "";
	display: block;
	position: absolute;
	left: 50%;
	top: 50%;
	margin: -6px 0 0 -1px;
	backface-visibility: hidden;
	width: 2px;
	height: 12px;
	background-color: white;
	will-change: transform;
	transition-property: transform;
	transition-duration: 0.2s;
}

.rolls__angle::before {
	transform: rotate(-0.25turn);
	transform: rotate(-0.25turn) translateZ(0);
}

.rolls__header.is-open .rolls__angle::before, .rolls__header.is-open .rolls__angle::after {
	transform: rotate(0);
	transform: rotate(0) translateZ(0);
}

.rolls__header,
.rolls__item,
.rolls__panel {
	padding: 10px;
	margin-bottom: 10px;
}

.rolls__header:last-child,
.rolls__item:last-child,
.rolls__panel:last-child {
	margin-bottom: 0;
}

.rolls__header {
	background-color: #fffcc1;
}

.rolls__item {
	background-color: #ffdbd4;
}

.rolls__panel {
	background-color: #c6fdcf;
}

/* rolls end */
